import React from 'react';
import AMCIcon from "views/mobileView/components/atoms/AMCIcon"
import { CurrencyFormat } from "views/mobileView/components/atoms/CurrencyFormat"
import cssVar from "views/mobileView/styles"

const TransactionCard = ({trx,i,type}) => {
    return (
        <div key={i} style={{ padding: "4%", borderBottom: '1px solid ' + cssVar.getPropertyValue('--light') }}>
            <div className='smallTextLight' style={{ marginBottom: "1%" }}>{new Date(trx.TradeDate).toDateString()}</div>
            <div style={{ display:"flex",marginBottom:"1%" }}>
                <div className='header2dark' style={{flex:1}}>{trx.TransactionType}</div>
                <div className='bodyTextDark'>{CurrencyFormat(trx.Amount)}</div>
            </div>
            <div style={{ display:"flex",marginBottom:"1%", gap:"4%" }}>
                <div style={{display:'flex',gap:"4%", alignItems: 'center',flex:1}}>
                    {
                        type === 3?
                            <>
                                <AMCIcon amcName={trx.AMCName} height='24px' width='24px'/>
                                <div className="bodyTextDark" style={{flex:1}}>{trx.ETFName}</div>
                            </>
                            :
                            <></>    
                    }
                </div>
                { type === 4? <></> : <div className='smallTextLight'>{trx.Units} Units</div>}
            </div>
        </div>
    )
}

export default TransactionCard